<script>
export default {
  data () {
    return {
      toolbarButtons: [
        { key: 'create-group', name: '' },
        { key: 'update-group', name: '' },
        { key: 'delete-group', name: '' },
        { key: 'create-define', name: '' },
        { key: 'update-define', name: '' },
        { key: 'delete-define', name: '' },
        { key: 'execute', name: '' },
        { key: 'moveup', name: '' },
        { key: 'movedown', name: '' },
        { key: 'import', name: '' },
        { key: 'export', name: '' },
        { key: 'refresh', name: '' }
      ],
      disables: ['delete-group', 'update-define', 'import']
    }
  },
  methods: {
    handleCheckboxChanged (check) {
      if (check) {
        this.$refs.toolbar.disableButtons(['delete-group', 'update-define', 'import'])
      } else {
        this.$refs.toolbar.enableButtons(['delete-group', 'update-define', 'import'])
      }
    }
  }
}
</script>


