<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;base-tree :load=\&quot;loadNode\&quot; lazy&gt;&lt;/base-tree&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      nodeId: 1\n    }\n  },\n  methods: {\n    loadNode(node, resolve) {\n      if (node.level === 0) {\n        // data,\n        const data = this.treeDataList.filter(item =&gt; item.parent === null)\n        return resolve(data)\n      }\n\n      if (node.level &gt; 0) {\n        // data,\n        const data = this.treeDataList.filter(item =&gt; item.parent === node.data.id)\n        // \n        resolve(data, this.currentNodeData.id)\n      }\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
