  <base-tree :load="loadNode" lazy=""></base-tree>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">base-tree</span>
    <span class="hljs-attr">:load</span>=<span class="hljs-string">"loadNode"</span>
