<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;base-tree node-key=\&quot;id\&quot; show-searchbox=\&quot;\&quot; :load=\&quot;loadNode2\&quot; lazy @on-search-change=\&quot;handleSearchChanged\&quot; @on-search-selected=\&quot;handleSearchSelected\&quot;&gt;&lt;/base-tree&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      currentNodeData: {}\n    }\n  },\n  methods: {\n    handleSearchChanged (text, resolve) {\n      const data = this.treeDataList.filter(item =&gt; item.label.includes(text))\n      resolve(data)\n    },\n    handleSearchSelected (nodeData, resolve) {\n      this.currentNodeData = nodeData\n    },\n    loadNode2(node, resolve) {\n      if (node.level === 0) {\n        // data,\n        const data = this.treeDataList.filter(item =&gt; item.parent === null)\n        return resolve(data)\n      }\n\n      if (node.level &gt; 0) {\n        // data,\n        const data = this.treeDataList.filter(item =&gt; item.parent === node.data.id)\n        // \n        resolve(data, this.currentNodeData.id)\n      }\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
