<script>
export default {
  data () {
    return {
      treeData: [
        {
          id: 1,
          label: ' 1',
          hasChildren: true,
          children: [{
            id: 4,
            label: ' 1-1',
            hasChildren: true,
            children: [{
              id: 9,
              label: ' 1-1-1',
              hasChildren: false
            }, {
              id: 10,
              label: ' 1-1-2',
              hasChildren: false
            }]
          }]
        }, {
          id: 2,
          label: ' 2',
          hasChildren: true,
          children: [{
            id: 5,
            label: ' 2-1',
            hasChildren: false
          }, {
            id: 6,
            label: ' 2-2',
            hasChildren: false
          }]
        }
      ],
      treeDataList: [
        {
          id: 1,
          label: ' 1',
          hasChildren: true,
          nodeNamePaths: ' 1',
          nodePaths: '1',
          parent: null,
        },
        {
          id: 4,
          label: ' 1-1',
          nodeNamePaths: ' 1/ 1-1',
          nodePaths: '1/4',
          hasChildren: true,
          parent: 1
        },
        {
          id: 9,
          label: ' 1-1-1',
          hasChildren: false,
          nodeNamePaths: ' 1/ 1-1/ 1-1-1',
          nodePaths: '1/4/9',
          parent: 4
        },
        {
          id: 10,
          label: ' 1-1-2',
          hasChildren: false,
          nodeNamePaths: ' 1/ 1-1/ 1-1-2',
          nodePaths: '1/4/10',
          parent: 4
        },
        {
          id: 2,
          label: ' 2',
          hasChildren: true,
          nodeNamePaths: ' 2',
          nodePaths: '2',
          parent: null,
        },
        {
          id: 5,
          label: ' 2-1',
          hasChildren: false,
          nodeNamePaths: ' 2/ 2-1',
          nodePaths: '2/5',
          parent: 2
        },
        {
          id: 6,
          label: ' 2-2',
          hasChildren: false,
          nodeNamePaths: ' 2/ 2-2',
          nodePaths: '2/6',
          parent: 2
        }
      ],
      nodeId: 1,
      currentNodeData: {}
    }
  },
  methods: {
    filterNode(value, data) {
      if (!value) return true
      return data.label.indexOf(value) !== -1
    },
    handleSearchChanged (text) {
      this.$refs.filterTree.filter(text)
    },
    loadNode(node, resolve) {
      if (node.level === 0) {
        const data = [{
          id: 1,
          label: ' 1',
          hasChildren: true
        }]
        return resolve(data)
      }

      if (node.level > 0) {
        this.nodeId++
        const data = [{
          id: this.nodeId,
          label: ' ' + this.nodeId,
          hasChildren: true
        }]
        resolve(data)
      }
    },

    handleSearchChanged (text, resolve) {
      const data = this.treeDataList.filter(item => item.label.includes(text))
      resolve(data)
    },
    handleSearchSelected (nodeData, resolve) {
      this.currentNodeData = nodeData
    },
    loadNode2(node, resolve) {
      if (node.level === 0) {
        // data,
        const data = this.treeDataList.filter(item => item.parent === null)
        return resolve(data)
      }

      if (node.level > 0) {
        // data,
        const data = this.treeDataList.filter(item => item.parent === node.data.id)
        resolve(data)
      }
    }
  }
}
</script>


